import React from 'react';




const CommentsList = ({ comments }) => (
    <div id="comment-section">
    <h3> Comments </h3>
  {comments.map((comments, key) =>(
       <div key={key} className="comment">
        <h4> {comments.username} </h4>
        <p> {comments.text} </p>
     </div>

))}
</div>
);

export default CommentsList


